$blue: #228be6;
$gray: #495057;
$pink: #f06595;
@mixin button-color($color) {
    background-color: $color;
    &:hover{
        background: lighten($color: $color, $amount: 10%);
    }
    &:active{
        background: lighten($color: $color, $amount: 10%);
    }
    &.outline{
        color: $color;
        background: none;
        border: 1px solid $color;
        &:hover{
            background-color: $color;
            color: #fff;
        }
    }
}
.Button{
    display: inline-flex;
    color: white;
    font-weight: bold;
    outline: none;
    border-radius: 4px;
    border: none;
    cursor: pointer;
    padding-left: 1rem;
    padding-right: 1rem;
    align-items: center;
    background-color: $blue;
    
    &.large{
        height: 3rem;
        font-size: 1.25rem;
    }
    &.medium{
        height: 2.25rem;
        font-size: 1rem;
    }
    &.small{
        height: 1.75rem;
        font-size: 0.875rem;
    }
    &.blue{
        @include button-color($blue);
    }
    &.gray{
        @include button-color($gray);
    }
    &.pink{
        @include button-color($pink);
    }
    &:hover{
        background: lighten($color: $blue, $amount: 10%);
    }
    &:active{
        background: darken($blue, 10%);
    }
    & + &{
        margin: 1rem;
    }
    &.fullwidth{
        width: 100%;
        justify-content: center;
        &+&{
            margin-left: 0;
            margin-top: 1rem;
        }
    }
}